import React, { Component } from 'react';
import { ProcessIndicator, Select, Radio } from '../src';
import {
  processIndicatorSizes,
  processIndicatorStatuses,
} from '../src/processIndicator/ProcessIndicator';

export default class ProcessIndicatorDocs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: 'processing',
      size: 'sm',
    };
  }

  render() {
    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Process Indicator</h2>
              <p>
                A component for conveying that a process is occuring, and for communicating a
                successful or unsuccessful result
              </p>
            </div>
            <div className="col-md-6">
              <ProcessIndicator status={this.state.status} size={this.state.size} />
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <pre className="tw-docs-code">
                {`<ProcessIndicator
  status={"${this.state.status}"}
  size={"${this.state.size}"}
/>`}
              </pre>
            </div>
            <div className="col-md-6">
              <div className="m-t-3" />
              <label htmlFor="select-size">Size</label>
              <Select
                id="select-size"
                selected={
                  this.state.size ? { value: this.state.size, label: this.state.size } : null
                }
                options={processIndicatorSizes.map(size => ({ value: size, label: size }))}
                onChange={selection => this.setState({ size: selection ? selection.value : null })}
              />
              <div className="m-t-3" />
              <label htmlFor="select-status">Status</label>
              {processIndicatorStatuses.map(statusOption => (
                <Radio
                  id={statusOption}
                  key={statusOption}
                  label={statusOption}
                  name={statusOption}
                  disabled={
                    (this.state.status === 'failed' && statusOption === 'succeeded') ||
                    (this.state.status === 'succeeded' && statusOption === 'failed')
                  }
                  checked={this.state.status === statusOption}
                  onChange={() => this.setState({ status: statusOption })}
                />
              ))}
            </div>
          </div>
        </section>
      </div>
    );
  }
}



